import { IRxDisabledProp, IRxStandardProps } from '@helix/platform/view/api';

export interface IActionButtonProperties extends IRxStandardProps, IRxDisabledProp {
  cls?: string;
  fieldId?: string;
  icon?: string;
  iconAlignment?: ActionButtonIconAlignment;
  iconCls?: string;
  label?: string;
  labelKey?: string;
  recordDefinitionName?: string;
  recordInstance?: string | any;
  size?: ActionButtonSize;
  style?: ActionButtonStyle;
  // set by association view component for internal action buttons
  action?: string;
}

export enum ActionButtonStyle {
  Primary = 'primary',
  Secondary = 'secondary',
  Tertiary = 'tertiary'
}

export enum DeprecatedActionButtonStyle {
  Link = 'link',
  Clear = 'clear'
}

export enum ActionButtonSize {
  Small = 'small',
  Default = 'default',
  Large = 'large'
}

export enum ActionButtonIconAlignment {
  Left = 'left',
  Right = 'right'
}
